/* HEADER */
.page__content {
  height: 100%;
}
.page__header {
  position: fixed;
  top: 0;
  z-index: 9;
  width: 100%;
  height: 70px;
  background: #000;
  color: #fff;
}
.page__menu {
  height: 100%;
  display: flex;
  justify-content: center;
}
.page__list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.page__link {
  color: #fff;
  font-size: 18px;
}
.page__link:not(:last-child) {
  margin-right: 20px;
}
.page__footer {
  position: absolute;
  width: 100%;
  height: 300px;
  background: #000;
  color: #fff;
}

#container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.panel {
  position: absolute;
  will-change: transform;
}